.Card {
    height: 110px;
    width: 110px;
    border: none;
    background: none;
    padding-left: 0%;
    padding-right: 0%;
    margin-bottom: 12px;
}

.Photo {
    object-fit: fill;
    width: 99%;
    height: 99%;
    border-radius: 5%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.RecipeName {
    font-size: 20px;
    padding-top: 5px;
    margin-top: 0%;
    transform: translateY(-20%);
    white-space: wrap;
    overflow: hidden;
    -webkit-text-fill-color: rgba(255, 255, 255, 0.3);
}